/* The Modal (background) */
.modal {
    /* position: fixed; */
    z-index: 1; /* Sit on top */
    padding: 100px 0; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    /* overflow: auto; */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    min-height: 100vh;
  }
  
  /* Modal Content */
  .modal-content {
    /* background-color: #fefefe; */
    z-index: 2;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
  }
  
  /* The Close Button */
  .close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }

  .modal-detail {
    display: flex;
    padding-top: 10px;
  }

  .modal-images{
    flex: 8;
  }
  .modal-images img {
    padding: 5px 0;
  }
  .modal-description{
    flex: 2;
    padding: 0 10px;
  }

.modal-description h3 {
  margin: 5px 0;
}

.modal-description .skills b{
  color: antiquewhite;
}

.modal-description a {
  color: darkkhaki;
}